import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpErrorResponse } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { map, catchError, tap } from 'rxjs/operators';




@Injectable()
export class AirconditionerService {

  allArticlesUrl = "https://timpringangularprojct.herokuapp.com/airconditioner1";

  message: string;
  constructor(private http: HttpClient) {
      }

  getProducts(): Observable<any> {

    return this.http.get( endpoint).pipe(
      map(this.extractData));
  }

  private extractData(res: Response) {
    let body = res;
    return body || { };
  }


    getCustomersList():Observable<any> {


    return this.http.get(this.allArticlesUrl);
    //return this.http.get<Obiekty[]>(this.allArticlesUrl).pipe(map(res => res),catchError(this.handleError));

    //return this.http.get<Obiekty[]>(`${this.allArticlesUrl}`).pipe(map(res => res));


  }
  private handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {

      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead

      // TODO: better job of transforming error for user consumption
      console.log(`${operation} failed: ${error.message}`);

      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }

}
const endpoint = 'https://timpringangularprojct.herokuapp.com/airconditioner1';
const httpOptions = {headers: new HttpHeaders({'Content-Type':  'application/json'})};
