import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import {AirconditionerComponent} from "./airconditioner/airconditioner.component";
import {HydroComponent} from "./hydro/hydro.component";
import {AlarmComponent} from "./alarm/alarm.component";
import {RouterModule, Routes} from "@angular/router";
import {HouseComponent} from "./house/house.component";
import {LightComponent} from "./light/light.component";

const appRoutes: Routes=[
  {path: 'light',
    component: LightComponent,},
  {path: 'airconditioner',
    component: AirconditionerComponent,},
  {path: 'alarm',
    component: AlarmComponent,},
  {path: 'house',
    component: HouseComponent,},
  {path: 'hydro',
    component: HydroComponent,},

]

@NgModule({
  imports: [
    CommonModule,
    RouterModule.forRoot(appRoutes)
  ],
  exports:[RouterModule],
  declarations: []
})
export class AppRoutingModule { }
export const routingComponents = [AirconditionerComponent,AlarmComponent,HouseComponent,HydroComponent,LightComponent]
